import React,{Component} from 'react';
import './index.scss';
import {TabBar} from 'antd-mobile';
import {connect} from 'react-redux'
// @connect(
//     state=>{
//         selectedTab:state.selectedTab
//     }
// )
export class MyFoot extends Component{
    state={
        // foots:[
        //     {txt:'',path:'/app/home'},
        //     {txt:'',path:'/app/home'},
        //     {txt:'',path:'/app/home'},
        //     {txt:'',path:'/app/my',name:'my'}
        // ],
        selectedTab:'home'
    }
    componentWillMount(){
        const {location,dispatch} =this.props;
        console.log(this.props)
        var pathname=location.pathname.split('/app/');
        console.log(pathname)
        // dispatch(changeSelectedTab(pathname[1]))
        this.setState({
            selectedTab:pathname[1]
        })
    }
    render(){
        const {history,dispatch} = this.props;
        const {selectedTab} =this.state
        return(
            <div className="footer">
            <TabBar
                barTintColor="white"
            >
                <TabBar.Item
                    title=''
                    key='home'
                    icon={<div
                        className='iconfont icon-home2'
                        style={{
                        width:'22px',
                        height:'22px'
                    }} />}
                    selected={selectedTab==='home'}
                    selectedIcon={
                        <div 
                        className='iconfont icon-home1'
                            style={{
                            width: '22px',
                            height: '22px'
                           }}/>
                    }
                    data-seen='logId'
                    onPress={()=>{
                        // dispatch(changeSelectedTab('home'))
                        this.setState({
                            selectedTab:'home'
                        })
                        history.push('/app/home')
                    }}
                ></TabBar.Item>
                <TabBar.Item
                    title=''
                    key='shopping'
                    icon={<div 
                        className='iconfont icon-store_icon'
                        style={{
                        width:'22px',
                        height:'22px' 
                    }} />}
                    selected={selectedTab==='shopping'}
                    selectedIcon={
                        <div 
                        className='iconfont icon-icon_shangcheng-mian'
                        style={{
                            width: '22px',
                            height: '22px' }}/>
                    }
                    data-seen='logId'
                    onPress={()=>{
                        // dispatch(changeSelectedTab('shopping'))
                        this.setState({
                            selectedTab:'shopping'
                        })
                        history.push('/app/shopping')
                    }}
                ></TabBar.Item>
                <TabBar.Item
                    title=''
                    key='shoppingcar'
                    icon={<div 
                        className='iconfont icon-gouwuche'
                        style={{
                        width:'22px',
                        height:'22px'
                    }} />}
                    selected={selectedTab==='shoppingcar'}
                    selectedIcon={
                        <div 
                        className='iconfont icon-03f'
                        style={{
                            width: '22px',
                            height: '22px'
                             }}/>
                    }
                    data-seen='logId'
                    onPress={()=>{
                        // dispatch(changeSelectedTab('shoppingcar'))
                        this.setState({
                            selectedTab:'shoppingcar'
                        })
                        history.push('/app/shoppingcar')
                    }}
                ></TabBar.Item>
                <TabBar.Item
                    title=''
                    key='my'
                    icon={<div 
                        className='iconfont icon-weibiaoti2fuzhi12'
                        style={{
                        width:'22px',
                        height:'22px' 
                    }} />}
                    selected={selectedTab==='my'}
                    selectedIcon={
                        <div 
                        className='iconfont icon-mys'
                        style={{
                            width: '22px',
                            height: '22px'}}/>
                    }
                    data-seen='logId'
                    onPress={()=>{
                        // dispatch(changeSelectedTab('my'))
                        this.setState({
                            selectedTab:'my'
                        })
                        history.push('/app/my')
                    }}
                ></TabBar.Item>
            </TabBar>
            </div>
        )
    }
}


