import React,{Component} from 'react';
import './index.scss'
import { Head } from '../../components/head';
import {NavBar,Icon,Carousel,NoticeBar} from 'antd-mobile'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import { ajaxSend,get_vip } from '../../actions';
@connect(
    state=>{
       return{
        viptip:state.viptip,
        list3:state.list3,
        brand:state.brand,
        brand2:state.brand2,
       }
    }
)
export default class Home extends Component{

    componentWillMount(){
        const {dispatch}=this.props;
        dispatch(ajaxSend({
            config:{
                method:'get',
                url:'/vipinfo'
            },
           success(data){
               dispatch(get_vip(data))
           }
        }))
    }
    render(){
        console.log(this.props.brand2)
        const {viptip,list3,brand,brand2} =this.props
        const usertel=localStorage.usertel
        return(
            
            <div>
                <NavBar
                    mode='dark'
                    rightContent={[<i key='0' className='iconfont icon-xin' />,<i key='1' className='iconfont icon-duihuakuang4' style={{marginLeft:20}} />]}
                >
                
                </NavBar>  
                <div className="VIPinfo">
                    <Carousel className="my-carousel"
                            vertical
                            dots={false}
                            dragging={false}
                            swiping={false}
                            autoplay
                            infinite
                            speed={500}
                        >
                            {
                                viptip.map((vip,index)=>{
                                    return(
                                        <div className="v-item" style={{height:36,lineHeight:'36px',textAlign:'center',color:'#999'}} key={index} > {vip.message}</div> 
                                    )
                                       
                                })
                            }
                        </Carousel>
                    <h3>8</h3>
                    <Link to={usertel?'/vip':'/register'}></Link>
                </div>  
                <ul className="index-nav">
                     {
                         list3.map((l,index)=>{
                            return(
                                <Link to={'/sort?type='+l.type }  key={index}>
                                    <li>
                                        <i className={'iconfont ' + l.icon} ></i>
                                        <p>{l.txt}</p>
                                    </li> 
                                </Link>
                            )
                        })
                     }       
                </ul>
                <div className='brand'>
                    <h3></h3>
                    <ul className="index-brand">
                        {
                            brand.map((b,index)=>{
                                return(
                                    <Link to={'/sort?type='+b.type }  key={index}>
                                        <li>
                                            <img src={b.img} alt=""/>
                                        </li> 
                                    </Link>
                                )
                            })
                        }
                    </ul>
                </div>
                <div className="index-list">
                    <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
                        9:305:30
                    </NoticeBar>
                        {
                            brand2.map((b,index)=>{
                                return(
                                    <Link to={'/sort?type='+b.type }  key={index}>
                                        <li>
                                            <img src={b.img} alt=""/>
                                        </li> 
                                    </Link>
                                )
                            })
                        }
                </div>
            </div>
        )
    }
}


