import React,{Component} from 'react';
import './index.scss'
import { Head } from '../../components/head';
import { Button, WhiteSpace, WingBlank,InputItem, Toast,List } from 'antd-mobile';
import {connect} from 'react-redux'
import { ajaxSend, get_timerMsg } from '../../actions';
import { GoHome } from '../../components/gohome';

@connect(
    state=>{
        return{
            timerMsg:state.timerMsg,

        }
    }
   
)
export default class Register extends Component{
    getCode=()=>{
        const {dispatch} = this.props;
        const usertel=this.refs.phone.state.value.replace(/\s+/g,'')
        const reg=/^[1][3,4,5,7,8,9][0-9]{9}$/
        console.log(usertel)
        var timer=null;
        var count=60;
        timer=setInterval(()=>{
            count--
            if(count==0){
                clearInterval(timer)
                dispatch(get_timerMsg(''))
                count=60;
            }else{
                dispatch(get_timerMsg(count+''))
            }
            console.log(count)
        },1000)
        if(reg.test(usertel)){
            Toast.loading("...",3);
            dispatch(ajaxSend({
                config:{
                    method:'POST',
                    data:{usertel},
                    url:'/register'
                },
                success(data){
                    Toast.hide();
                    Toast.success("!")
                },
                error(){
                    Toast.hide();
                    Toast.offline("!") 
                }
            }))
        }else{
            Toast.info('')
        }

    }
    login=()=>{
        const {dispatch,history} = this.props;
        const usertel=this.refs.phone.state.value.replace(/\s+/g,'');
        const telcode=this.refs.code.state.value.replace(/\s+/g,'');
        console.log(usertel,telcode);
        Toast.loading("...",3);
        dispatch(ajaxSend({
            config:{
                method:'POST',
                data:{usertel,telcode},
                url:'/login'
            },
            success(data){
                console.log(data)
                if(data==null){
                    Toast.hide();
                    Toast.offline("!")
                }else{
                    Toast.hide();
                    Toast.success("!")
                    if(usertel){
                        localStorage.usertel=usertel
                    }else{
                        localStorage.usertel=''
                    }
                    var url=localStorage.prevUrl;
                    if(url){
                        history.push(url)
                        localStorage.prevUrl='';
                    }else{
                        history.push('/app/my/')
                    }
                    
                }
            }
        }))
    } 

    
      
    render(){
        const {timerMsg}=this.props
        return(
            <div>
                <Head title='' show={true} history={this.props.history}></Head>
                <WhiteSpace size='lg' />
                <WingBlank>
                    <List>
                        <WhiteSpace size='lg' />
                        <InputItem
                            type="phone"
                            placeholder="" 
                            ref='phone'
                        ></InputItem>
                        <InputItem
                            type="number"
                            placeholder=""
                            ref='code'
                        ></InputItem>
                        <WhiteSpace size='lg' />
                        <Button type='primary' disabled={parseInt(timerMsg)?true:false}  onClick={this.getCode}>{timerMsg}</Button>
                    </List>
                    
                    <WhiteSpace size='lg' />
                    <Button type='warning' className='login' onClick={this.login}></Button>
                    <WhiteSpace size='lg' />
                    <h3 className='reg'><span></span></h3>
                    <div className="other">
                        <h3><s></s><s></s></h3>
                        <div className="other-icon">
                        <i className='iconfont icon-qq'></i>
                        <i className='iconfont icon-weixin'></i>
                        <i className='iconfont icon-weibo'></i>
                        </div>
                    </div>
                </WingBlank>
                <GoHome/>
            </div>
        )
    }
}


