import React , {Component} from 'react';
import {Route} from 'react-router-dom';
import Swipe from '../../components/swipe'
import {connect} from 'react-redux'
@connect(
   state=>{
    return {
        imgs:state.imgs,
    }
   }
)
export default class Guide extends Component{
    componentWillMount(){

        const {history} =this.props;
        console.log(localStorage.reactCount)
        if(localStorage.reactCount&&localStorage.reactCount>3){
            localStorage.reactCount++;
            history.push('/app/')
        }else{
            if(localStorage.reactCount){
                localStorage.reactCount++
            }else{
                localStorage.reactCount=1
            }
        }
        setTimeout(()=>{
            history.push('/app/')
        },3000)
        
    }
    goApp=(index)=>{
        const {history,imgs} =this.props;
        if(index==imgs.length-1){
            history.push('/app/')
        }
    }
    render(){
        const img=this.props.imgs.map((img,index)=>{
            return(
                <img onClick={()=>this.goApp(index)} src={img} key={index} style={{width:'100%',height:'100%'}} alt=""/>
                )
            })


        return(
            <div className='box'>
                <Swipe id = 'guide' swiperOptions={{speed:1000,resistanceRatio:0}}>{img}</Swipe>
            </div>
        )
    }
}


