import React, { Component } from 'react';
import './index.scss'
import { Toast, WingBlank,List, Radio, Flex ,Modal} from 'antd-mobile';
import { Head } from '../../components/head';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom'
import { ajaxSend, get_detail,changeValue,changeSizeValue, changeColorFlag, changeSizeFlag,addCount,descCount, changeCount } from '../../actions';
import { GoHome } from '../../components/gohome';
const RadioItem = Radio.RadioItem;
const alert = Modal.alert;
@connect(
    state => {
        return {
            goods: state.goods,
            value:state.value,
            value2:state.value2,
            colorFlag:state.colorFlag,
            data:state.data,
            data2:state.data2,
            sizeFlag:state.sizeFlag,
            count:state.count
        }
    }
)
export default class Detail extends Component {
    onChange = (value,colorFlag) => {
            this.props.dispatch(changeValue(value))
            this.props.dispatch(changeColorFlag(colorFlag=!colorFlag))
    };
    onChangeSize=(value,sizeFlag) => {
        this.props.dispatch(changeSizeValue(value))
        this.props.dispatch(changeSizeFlag(sizeFlag=!sizeFlag))
   };
   buyone=()=>{
    let {dispatch,goods,data,data2,value,value2,count,colorFlag,sizeFlag,history,location}=this.props
    const goodsSize=data2[value2]&&data2[value2].label
    const goodsColor=data[value]&&data[value].label
    console.log(goodsSize,goodsColor,count)
    console.log(goods)
    const usertel=localStorage.usertel;
    if(usertel){
      if(colorFlag&&sizeFlag){//
          goods={...goods,...{goodsSize,goodsColor,goodsCount:count,usertel,checkFlag:false}}
          console.log(goods)
          dispatch(ajaxSend({
              config:{
                  method:'get',
              params:{
                  goods
              },
              url:'/shoppingcar'
              },
              success(data){
                  if(data.code==1){
                    //   Toast.info(,1)
                    console.log(data)
                      history.push('/settle?goodsId='+goods.goodsId+'&usertel='+usertel)
                      
                  }else{
                      Toast.info(data.msg,1)
                  }
                  this.props.dispatch(changeSizeFlag(false))
                  this.props.dispatch(changeColorFlag(false))
              }
          }))
         
  
        }else{
            Toast.info('',1)
        }
    }else{
      alert('','',[
          {text:'',onPress:()=>console.log('')},
          {text:'',onPress:()=>{
              localStorage.prevUrl=location.pathname+location.search
              history.push('/register')
          }}
      ])
    }
   }
   buysome=()=>{
      let {dispatch,goods,data,data2,value,value2,count,colorFlag,sizeFlag,history,location}=this.props
      const goodsSize=data2[value2]&&data2[value2].label
      const goodsColor=data[value]&&data[value].label
      console.log(goodsSize,goodsColor,count)
      console.log(goods)
      const usertel=localStorage.usertel;
      if(usertel){
        if(colorFlag&&sizeFlag){//
            goods={...goods,...{goodsSize,goodsColor,goodsCount:count,usertel,checkFlag:false}}
            console.log(goods)
            var  that=this
            dispatch(ajaxSend({
                config:{
                    method:'get',
                params:{
                    goods
                },
                url:'/shoppingcar'
                },
                success(data){
                    if(data.code==1){
                        Toast.info(data.msg,1)
                        // history.push('/app/shoppingcar')
                        that.props.dispatch(changeSizeFlag(false))
                        that.props.dispatch(changeColorFlag(false))
                        alert('','',[
                            {text:'',onPress:()=>console.log('')},
                            {text:'',onPress:()=>history.push('/app/shoppingcar')}
                        ])
                    }else{
                        Toast.info(data.msg,1)
                    }
                    this.props.dispatch(changeSizeFlag(false))
                    this.props.dispatch(changeColorFlag(false))
                }
            }))
           
    
          }else{
            Toast.info('',1)
        }
      }else{
        alert('','',[
            {text:'',onPress:()=>console.log('')},
            {text:'',onPress:()=>{
                localStorage.prevUrl=location.pathname+location.search
                history.push('/register')
            }}
        ])
      }
      
   }
   changeCount=()=>{
       const {count} = this.props;
       console.log(this.refs.goodscount)
       this.refs.goodscount.value=count
   }
    componentWillMount() {
        Toast.loading("...", 10);
        const { dispatch, history, location } = this.props;
        const goodsId = location.search.split('?')[1]
        dispatch(ajaxSend({
            config: {
                method: "get",
                params: {
                    goodsId
                },
                url: '/detail'
            },
            success(data) {
                console.log(data)
                dispatch(get_detail(data))
                Toast.hide();
                Toast.success("!", 1)
            }
        }))
    }
    render() {
        const { goods,value,value2 ,colorFlag,data,data2,sizeFlag,dispatch,count} = this.props
        console.log(this.props)
        return (
            <div className='detail-box'>
                <Head title='Detail' show={true} history={this.props.history}></Head>

                <div className="detail-content">
                    <div className='de-img'>
                        <img src={goods.imgUrl} alt="" />
                    </div>
                    <div className="detail-top">
                        <WingBlank>
                            <div className='de-content'>
                                <div className="de-price">
                                    <h3><sub></sub>{goods.crazyPrice}</h3>
                                    <s>{goods.shopPrice}</s>
                                    <span>{goods.discount}</span>
                                    <p></p>
                                </div>
                                <div className="de-count">
                                    <p><span>{goods.stocknum}</span></p>
                                    <p><span>{goods.soldnum}</span></p>
                                </div>
                            </div>
                            <div className='de-vip'>
                                <span>SVIP</span>
                                <p>SVIP:{goods.crazyPrice}23    </p>
                                <i className='iconfont icon-you'></i>
                            </div>
                            <div className="de-name">
                                <p>{goods.goodsName}</p>
                            </div>
                        </WingBlank>
                    </div>
                    {/* <WingBlank>
                    <div>
                        <span></span>19910 29920
                    </div>
                </WingBlank> */}
                    <div className="detail-button">
                        <WingBlank>
                            <div className="de-check ">
                                <div className="de-color de-com">
                                    <h3></h3>
                                    <ul>
                                        {
                                            data.map(i=>(
                                                <li 
                                                style={value == i.value&&colorFlag==true?{border:'1px solid #cd0000'}:{}}
                                                 onChange={() => this.onChange(i.value,colorFlag)} 
                                                 key={i.value} >
                                                 <label htmlFor={i.label}>{i.label}</label><input  checked={value === i.value}type="checkbox" id={i.label} style={{display:'none'}}/></li>
                                            ))
                                        }
                                    </ul>
                                </div>
                                <div className="de-size de-com">
                                    <h3></h3>
                                    <ul>
                                    {
                                        data2.map(i=>(
                                            <li 
                                            style={value2 == i.value&&sizeFlag==true?{border:'1px solid #cd0000'}:{}}
                                                onChange={() => this.onChangeSize(i.value,sizeFlag)} 
                                                key={i.value} >
                                                <label htmlFor={i.label}>{i.label}</label><input  checked={value2 === i.value}type="checkbox" id={i.label} style={{display:'none'}}/></li>
                                            ))
                                        }
                                    </ul>
                                </div>
                                <div className=' de-com'>
                                    <h3></h3>
                                    <div className="de-goodscount">
                                        <p  ref='descNum' onClick={count>1?()=>dispatch(descCount(1)):console.log('')}>-</p> 
                                        <input type="number" ref='goodscount' value={count} onChange={this.changeCount}/> 
                                        <p  ref='addNum'  onClick={()=>dispatch(addCount(1))}> +</p>
                                    </div>
                                </div>
                                
                            </div>
                            <div className="de-eval">
                                <h3><i className='iconfont icon-you'></i></h3>
                            </div>
                        </WingBlank>
                    </div>
                </div>
                <ul className="de-footer">
                    <li style={{ width: '15%' }}>
                        <i className='iconfont icon-serve'></i>
                        <span></span>
                    </li>
                    <li style={{ width: '15%' }}>
                        <i className='iconfont icon-xin'></i>
                        <span>111</span>
                    </li>
                    <li style={{ background: 'linear-gradient(90deg,#666,#333)', width: '25%' ,}}  onClick={this.buysome}>
                        {/* <h3><sub></sub>{goods.crazyPrice / 1 + 23}</h3> */}
                        <p style={{fontSize:16}}></p>
                    </li>
                    <li style={{ background: 'linear-gradient(90deg,#FF8040,#FF5809)', width: '25%',fontSize:30 }} onClick={this.buyone}>
                        {/* <h3><sub></sub>{goods.crazyPrice}</h3> */}
                        <p style={{fontSize:16}}></p>
                    </li>
                </ul>
                <GoHome />
            </div>
        )
    }
}


