export const AJAX_SEND = "ajax_send";
/***
 * action
 * @param loading 
 * @param config ajax
 * @param success ajax
 * @param failed ajax
 * @param error ajax
 * @returns {{type: string, loading: *, config: {}, success: *, failed: *}}
 */
export const ajaxSend=({loading=true,config={},success,failed,error})=>{
    return{
        type:AJAX_SEND,
        loading,
        config,
        success,
        failed,
        error
    }
}

