import React,{Component} from 'react';
import './index.scss'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
@connect(
    state=>{
        return{
            goodscount:state.goodscount,
            brandcount:state.brandcount,
            footcount:state.footcount,
            list1:state.list1,
            list2:state.list2,
            my_bg:state.my_bg
        }
    }
)
export default class My extends Component{
    componentWillMount(){
        
        
        
    }
    render(){
        const {my_bg,list2,list1,goodscount,brandcount,footcount,match}=this.props;
        const usertel=localStorage.usertel
        console.log(usertel)
        return(
            <div>
               <div className="header clearfix" 
                    style={{background:'url('+{my_bg}+') no-repect center'}}
               >
                    <div className="header-left">
                        <div className='icon'>
                            <i className='iconfont icon-touxiang'></i>
                        </div>
                        <div>
                            <Link  to={usertel?'#':'/register'}>{usertel?usertel:' / '}</Link>
                        </div>
                    </div>
                    <div className="header-right">
                        <div className="hr-top">
                            <i className='iconfont icon-icon_set_up'></i>
                            <Link  to='/set'>
                                <i className='iconfont icon-duihuakuang4'></i>
                            </Link>
                        </div>
                        <div className="hr-buttom">
                            <Link  to={usertel?'/vip':'/register'}>
                                <i className='iconfont icon-you'></i>
                            </Link>
                        </div>
                    </div>
               </div>
               <div>
                   <ul className="my-nav">
                       <li><span>{goodscount}</span><p></p></li>
                       <li><span>{brandcount}</span><p></p></li>
                       <li><span>{footcount}</span><p></p></li>
                    </ul> 
               </div>
               <div className='order'>
                    <Link to={usertel?'/order':'/register'}>
                            <h3><span ><i className='iconfont icon-you'></i></span></h3>
                    </Link>
                    <ul>
                        {
                            list1.map((l,index)=>{
                               return(
                                    <Link to={usertel?'#':'/register'} key={index}>
                                        <li>
                                            <i className={'iconfont ' + l.icon} ></i>
                                            <p>{l.txt}</p>
                                        </li>
                                    </Link>
                               )
                            })
                        }
                    </ul>
               </div>
               <ul className="my-list">
                    {
                        list2.map((l,index)=>{
                            return(
                                <Link to={usertel?'#':'/register'} key={index}>
                                    <li>
                                        <i className={'iconfont ' + l.icon} ></i>
                                        <p>{l.txt}</p>
                                    </li> 
                                </Link>
                            )
                        })
                    }
               </ul>
            </div>
        )
    }
}


