import React , {Component} from 'react';
import './index.scss';

import {NavBar,Icon} from 'antd-mobile'


export class Head extends Component{
    goback=()=>{
        console.log(this.props);
        const {history} =this.props;
        history.go(-1)
    }
    tosearch=()=>{
        console.log('111')
        const {history} =this.props;
        console.log(this.props)
        history.push('/search')
    }
    render(){
        const {title,show,car} =this.props
        return(
            <NavBar
            mode='dark'
            icon={show&&<Icon type='left' /> }
            onLeftClick={show?this.goback:console.log('')}
            // rightContent={show?[<i key='1' className='iconfont icon-gouwuche' onClick={this.tocar} />]:''}
            rightContent={<i key='0' className='iconfont icon-sousuo-' onClick={this.tosearch} />}
            
            >
               {title} 
            </NavBar>  
        )
    }
}


