import React,{Component} from 'react';
import './index.scss'
import { Head } from '../../components/head';
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import {List,Toast} from 'antd-mobile'
import { ajaxSend, shoppingcar, changeValue, changeCheckFlag ,descCount,addCount,checkFlagList,setSumPriceNum} from '../../actions';
 
@connect(
    state=>{
        return{
            ...state
        }
    }
)
export default class Shoppingcar extends Component{

    // changeCount=()=>{
    //     const {count} = this.props;
    //     console.log(this.refs.goodscount)
    //     this.refs.goodscount.value=count

    // }
    // onChange = (index,checkFlag) => {
    //     console.log(index,checkFlag)
    //     console.log(this.props)
    //     const {value}=this.props
    //     this.props.dispatch(changeValue(index))
    //     if(value==true){
    //         if(value==index){
    //             this.props.dispatch(changeCheckFlag(checkFlag=!checkFlag))
    //         }
    //     } 
        
    // }
    changeGoodsChecked=(id,checked)=>{
        const {dispatch}=this.props
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    id,checked
                },
                url:'/changeGoodsChecked'  
            },
            success(data){
                console.log(data)
            }
        }))
    }
    getTotal=()=>{
        const {dispatch,goodsList} = this.props
        var sumPrice = 0,sumNum = 0;
        goodsList.map((item,i)=>{
            if(item.checked){
                sumPrice += (item.goodsCount*item.crazyPrice)
                sumNum += (item.goodsCount*1)
            }
        })
        sumPrice = sumPrice.toFixed(2)
        dispatch(setSumPriceNum(sumPrice,sumNum))
    }
    descGoodsCount=(id,num,index)=>{
        num=-Math.abs(num)
        const {dispatch,goodsList}=this.props
        goodsList[index].goodsCount+=num

        dispatch(shoppingcar(goodsList))
         this.forceUpdate()
    //    this.changeGoodsCount(id,num,index)
    }
    addGoodsCount=(id,num,index)=>{
        num= Math.abs(num)
        const {dispatch,goodsList}=this.props
        console.log(goodsList[index])
        goodsList[index].goodsCount+=num
        console.log(goodsList[index].goodsCount)
        dispatch(shoppingcar(goodsList))
         this.forceUpdate()
        // this.changeGoodsCount(id,num,index)
    }
    checkall=()=>{
       var {checkFlag,dispatch,goodsList} = this.props
       if(checkFlag){
        goodsList.map((goods,i)=>{goodsList[i].checked=false})
           dispatch(changeCheckFlag(false))
       }else{
        goodsList.map((goods,i)=>{goodsList[i].checked=true})
            dispatch(changeCheckFlag(true))
       }
       this.forceUpdate()
        
    }
    checkOneList=(i)=>{
        var {count,dispatch,checkFlag,goodsList} = this.props;
        console.log('checkOne')
        var counts = 0
        console.log(goodsList)
        console.log(goodsList[i].checked)
        if(goodsList[i].checked){
            goodsList[i].checked = false
        }else{
            goodsList[i].checked = true
        }
        this.changeGoodsChecked(goodsList[i]._id,goodsList[i].checked)
        goodsList.map((goods,i)=>{
            
            if(goods.checked){
                
                counts ++
            }else{
                counts --
            }
        }) 
        if(counts==goodsList.length){
            checkFlag= true
            dispatch(changeCheckFlag(checkFlag))
        }else{
            checkFlag = false
            dispatch(changeCheckFlag(checkFlag))
        }
        dispatch(shoppingcar(goodsList))
        this.forceUpdate()
    }      
      
    deleteOne=(id)=>{
        const usertel=localStorage.usertel
        const {dispatch}=this.props
        const that=this;
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    id
                },
                url:'/deletegoods'  
            },
            success(data){
                console.log(data)
                if(data.code==1){
                   setTimeout(()=>{
                    that.getGoodsList(usertel)
                    that.forceUpdate()
                   },300)
                }  
            }
        }))    
    }
    getGoodsList=(usertel)=>{
        const {dispatch}=this.props
        console.log(usertel)
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    usertel
                },
                url:'/getgoods'  
            },
            success(data){
                data.map((item,i)=>{
                    data[i].checked = false
                })

                dispatch(shoppingcar(data))
            }
        }))
    }
    settle=(sumPrice)=>{
        if(sumPrice!=0){
        const usertel=localStorage.usertel
        const {dispatch,goodsList}=this.props
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    usertel
                },
                url:'/deleteall'  
            },
            success(data){
                if(data.code==1){
                    Toast.success('',1)
                    setTimeout(()=>{
                        dispatch(setSumPriceNum(0,0))
                       
                    },300)
                    
                }
            }
        }))    
    }else{
        Toast.info('...',1)
    }
        
    }
    componentWillMount(){
        const usertel=localStorage.usertel
        const {goodsList} = this.props
        if(JSON.stringify(goodsList)==='[]'){
            this.getGoodsList(usertel)
        }
    }
    componentDidUpdate(){
        this.getTotal()
    }
    componentWillUpdate(){
        this.getTotal()
    }
    render(){
        const {goodsList,value,checkFlag,dispatch,sumPrice,sumNum}=this.props
        console.log(goodsList)
        return(
            <div>
                <Head title='Shoppingcar'></Head>
                <div className="car-content">
                    <h3> <i className='iconfont icon-jia'></i></h3>
                    <ul>
                        {
                         goodsList.map((goods,index)=>{
                             return(
                                    <li key={index}>
                                        <label 
                                        htmlFor={'check'+index}
                                        className={goods.checked?'iconfont icon-quangou':'iconfont icon-weixuanzhongyuanquan'} onClick={()=>this.checkOneList(index)}
                                        >
                                        

                                        </label>
                                            <input type="checkbox" style={{display:'none'}}  id={'check'+index} 
                                            />
                                        
                                        <div>
                                        <Link to={'/detail?'+goods.goodsId} > 
                                            <img src={goods.imgUrl} alt=""/>
                                            </Link>
                                            <div>
                                            
                                                <div>
                                                <Link to={'/detail?'+goods.goodsId} > 
                                                    <p style={{"WebkitBoxOrient": "vertical"}}>{goods.goodsName}</p>
                                                    </Link>
                                                    <i onClick={()=>{this.deleteOne(goods._id)}}></i>
                                                </div>
                                                <Link to={'/detail?'+goods.goodsId} > 
                                                <span>{goods.goodsColor} {goods.goodsSize}</span>
                                                </Link>
                                                <div>
                                                    <span>{(goods.crazyPrice*goods.goodsCount).toFixed(2)}</span>
                                                    <div className="car-goodscount">
                                                        <p onClick={goods.goodsCount>1?()=>this.descGoodsCount(goods._id,1,index):console.log('')}>-</p> 
                                                        <p  style={{width:70,margin:'0 5px'}}> {goods.goodsCount}</p>
                                                        <p  onClick={()=>this.addGoodsCount(goods._id,1,index)}> +</p>
                                                    </div>
                                                </div>
                                            </div>
                                         
                                        </div> 
                                    </li>   
                             )
                         })   
                        }
                       
                    </ul>
                </div>
                <div className="car-foot">
                    <div className="car-checkall">
                    

                        <label htmlFor='checkall'className={checkFlag==true?'iconfont icon-quangou':'iconfont icon-weixuanzhongyuanquan'}  onClick={this.checkall}></label>
                        <input type="checkbox"  id='checkall' style={{display:'none'}}/>
                        <span></span>
                    </div>
                    <div className="car-check">
                        <span><span>{sumPrice}</span></span>
                        <span onClick={()=>this.settle(sumPrice)} style={{color:'#fff',padding:'10px 20px',borderRadius:'50px',backgroundColor:'orange',marginLeft:'10px'}}>({sumNum})</span>
                    </div>
                </div>
            </div>
        )
    }
}


