import React,{Component} from 'react';
import './index.scss'
import {Toast,Carousel,} from 'antd-mobile';
import { Head } from '../../components/head';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom'
import { ajaxSend ,get_goods} from '../../actions';
import { GoHome } from '../../components/gohome';
@connect(
    state=>{
        return{
            list4:state.list4,
        }
    }
)
export default class Sort extends Component{
    componentWillMount(){
        Toast.loading("...",10);
        const {dispatch,location} = this.props;
        const type=decodeURIComponent(new URLSearchParams(location.search).get('type'))
        dispatch(ajaxSend({
            config:{
                method:"get",
                params:{
                    limit:18,
                    type
                },
                url:'/goodstype'
            },
            success(data){
                console.log(data)
                dispatch(get_goods(data))
                Toast.hide();
                Toast.success("!",1)
            }
        }))
    }
    render(){
        console.log(this.props)
        const {list4,location} =this.props
        const type=decodeURIComponent(new URLSearchParams(location.search).get('type'))
        console.log(list4)
        return(
            <div>
                <Head title={type} show={true}  history={this.props.history}></Head>
                <div className="shop-list" style={{marginTop:60}}>
                    <ul>
                        {
                            list4.map((item,index)=>{
                                return(
                                    <Link to={'/detail?'+item.goodsId} key={index}>
                                        <li>
                                            <img src={item.imgUrl} alt=""/>
                                            <h4><sub></sub>{item.crazyPrice}<s><sub></sub>{item.shopPrice}</s></h4>
                                            <p style={{color:'#cd0000',fontSize:'12px'}}>{item.flashPrice}</p>
                                            <p>{item.goodsName}</p>
                                        </li>
                                    </Link>
                                )
                            })
                        }
                    </ul>
                </div>
               <GoHome />
            </div>
        )
    }
}


