import React,{ Component } from "react";
import './index.scss';
import{Switch,Route,Redirect} from 'react-router-dom'
import My from "../my";
import { MyFoot } from '../../components/foot';
import Shopping from "../shopping";
import Shoppingcar from "../shoopingcar";
import Home from "../home";

export default class App extends Component{
    render(){
        return(
            <div className="box">
                <Switch>
                    <Route path='/app/home' component={Home}  />
                    <Route path='/app/shopping' component={Shopping}  />
                    <Route path='/app/shoppingcar' component={Shoppingcar}  />
                    <Route path='/app/my/:takon?' component={My} static />
                    <Route render={()=><Redirect to='/app/home'/>} />
                </Switch>
                <MyFoot {...this.props}/>
            </div>
        )
    }
}


