import React,{Component} from 'react';
import './index.scss'
import { Tabs, WhiteSpace } from 'antd-mobile';
import {connect} from 'react-redux';
import { Head } from '../../components/head';
import  {Link} from 'react-router-dom'
import { ajaxSend,changeValue, settle} from '../../actions';
@connect(
    state=>{
        return{
            tabs:state.tabs,
            oneGoods:state.oneGoods,
            value:state.value,
        }

    }
)

export default class Settle extends Component{
    onChange = (index) => {
        console.log(index)
        console.log(this.props)
        const {value}=this.props
        this.props.dispatch(changeValue(index))
        
    }
    changeGoodsCount=(id,num,index)=>{
        console.log(id,num,index)
        console.log(this.refs)
        console.log(this.refs[index])
        localStorage.changeCountInpIndex=index
        var that=this
        const {dispatch}=this.props
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    id,num
                },
                url:'/changeGoodsCount'  
            },
            success(data){
                
                localStorage.changedCount=data.goodsCount;
                console.log(data)
                if(data.code==1){
                    console.log(data.goodsCount)
                    window.location.reload()
                    // console.log(this.refs.index)
                    // that.refs.index.value=goods.goodsCount
                    
                }
            
                
            }
        }))
    }
    descGoodsCount=(id,num,index)=>{
        num=-Math.abs(num)
       this.changeGoodsCount(id,num,index)
    }
    addGoodsCount=(id,num,index)=>{
        num= Math.abs(num)
        this.changeGoodsCount(id,num,index)
    }
    componentWillMount(){
        const {dispatch}=this.props
        const usertel=localStorage.usertel
        if(usertel){
            const goodsId=new URLSearchParams(this.props.location.search).get('goodsId')
        dispatch(ajaxSend({
            config:{
                method:'get',
                params:{
                    usertel,goodsId
                },
                url:'/getonegoods'  
            },
            success(data){
                localStorage.checkallFlag=false
                localStorage.checkNum=data.length
                dispatch(settle(data))
            }
        }))
        }else{
            window.location.href='/app/home'
        }
        
    }
    render(){
        const {oneGoods}=this.props
        console.log(oneGoods)
        
       return(
        <div >
            <Head title={''} show={true} history={this.props.history}/>
            <div className='settle-content'>     
                <label htmlFor='check' >
                    <input type="checkbox"  id='check' checked={true} onChange={ this.onChange}/>
                </label> 
                <div>
                    <Link to={'/detail?'+oneGoods.goodsId}> 
                        <img src={oneGoods.imgUrl} alt=""/>
                    </Link>
                    <div>
                        <Link to={'/detail?'+oneGoods.goodsId} > 
                        <div>
                        <p style={{"WebkitBoxOrient": "vertical"}}>{oneGoods.goodsName}</p>
                        <i></i>
                        </div>
                            
                            <span>{oneGoods.goodsColor} {oneGoods.goodsSize}</span>
                        </Link>
                        <div>
                            <span>{(oneGoods.crazyPrice*oneGoods.goodsCount).toFixed(2)}</span>
                            <div className="car-goodscount">
                                <p onClick={oneGoods.goodsCount>1?()=>this.descGoodsCount(oneGoods._id,1):console.log('')}>-</p> 
                                <input type="number" ref='goodscount' value={oneGoods.goodsCount} onChange={this.changeCount}/> 
                                <p  onClick={()=>this.addGoodsCount(oneGoods._id,1)}> +</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
           <div className="settle-list">
           <ul>
                <li>
                    <h3></h3><span></span>
                </li>
                <li>
                    <h3></h3><span></span>
                </li>
                <li>
                    <h3></h3><span></span>
                </li>
                
            </ul>
           </div>
            <div className="car-foot settle-foot">
                    <div className="car-check">
                        <span><span>{(oneGoods.crazyPrice*oneGoods.goodsCount).toFixed(2)}</span></span>
                        <span style={{color:'#fff',padding:'10px 20px',borderRadius:'50px',backgroundColor:'orange',marginLeft:'10px'}}></span>
                    </div>
                </div>
        </div>
       )
    }
}   


