import React, {Component} from "react";
import "./index.scss";

import "../../utils/swiper-3.3.1.min.js";

export default class SwiperDemo extends Component{
    state = {
        flag:false
    }
    render(){
        console.log(this.props)
        return (
            <div ref="swiper" className="swiper-container" id={this.props.id}>
                <div className="swiper-wrapper">
                    {
                        this.props.children.map((child,index)=>{
                            return (
                                <div className="swiper-slide" key={index}>
                                    {child}
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }

    componentDidMount(){
        console.log(this.props);
        if(this.props.children.length&&!this.state.flag){
            const {id,swiperOptions} =  this.props; 
            this.refs.swiper = new Swiper("#"+id,swiperOptions);
            this.setState({
                flag:true
            })
        }
    }

    componentDidUpdate(){
        console.log(this.props);
        if(this.props.children.length&&!this.state.flag){
            const {id,swiperOptions} =  this.props; 
            this.refs.swiper = new Swiper("#"+id,swiperOptions);
            this.setState({
                flag:true
            })
        }
    }


}




