import React,{Component} from 'react';
import './index.scss';
import {Link} from 'react-router-dom'

export class GoHome extends Component{

        render(){
            return(
                <Link className='gohome' to='/app/home'><i className='iconfont icon-home' ></i></Link>
            )
        }
}



