import React,{Component} from 'react';
import './index.scss'
import { Head } from '../../components/head';
import {Toast,Carousel, WingBlank } from 'antd-mobile';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom'
import { ajaxSend ,get_goods} from '../../actions';
@connect(
    state=>{
        return{
            list4:state.list4,
            recommend1:state.recommend1,
            recommend2:state.recommend2,
            recommend3:state.recommend3,
            carousel:state.carousel
        }
    }
)
export default class Shopping extends Component{
    componentWillMount(){
        Toast.loading("...",10);
        const {dispatch,history} = this.props;
        dispatch(ajaxSend({
            config:{
                method:"get",
                params:{
                    limit:18
                },
                url:'/goods'
            },
            success(data){
                console.log(data)
                dispatch(get_goods(data))
                Toast.hide();
                Toast.success("!",1)
            }
        }))
    }
    render(){
        const {list4,carousel,recommend1,recommend2,recommend3}=this.props;
        return(
            <div>
                <Head title='' sear={true} history={this.props.history}></Head>
                <div className="shop-carousel" style={{marginTop:40}}>
                    
                        <Carousel
                            autoplay
                            infinite
                            dragging={false}
                            swiping={false}
                            speed={700}
                            >
                            {carousel.map((val,index )=> (
                                <Link
                                key={index}
                                to={'/sort?type='+val.type }
                                style={{ display: 'inline-block', width: '100%', hieght:300,}}
                                >
                                <img
                                    src={val.img}
                                    alt=""
                                    style={{height:240, width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                                </Link>
                            ))}
                        </Carousel>
                   
                </div>
                <div className="recommend">
                    <h3><i className='iconfont icon-yifu1'></i></h3>
                    <div className="shop-commend">
                        {recommend1.map((item,index)=>{
                            return (
                                <Link to={'/sort?type='+item.type } key={index}><img src={item.img} alt /></Link>
                            )
                                  
                        })}    
                    </div>
                </div>
                <div className="recommend">
                    <h3><i className='iconfont icon-19'></i></h3>
                    <div className="shop-commend">
                        {recommend2.map((item,index)=>{
                             return (
                                <Link to={'/sort?type='+item.type } key={index}><img src={item.img} alt /></Link>
                            )
                        })}
                    </div>
                </div>
                <div className="recommend">
                    <h3><i className='iconfont icon-paoxie'></i></h3>
                    <div className="shop-commend">
                    {recommend3.map((item,index)=>{
                         return (
                                <Link to={'/sort?type='+item.type } key={index}><img src={item.img} alt /></Link>
                            )
                    })}
                    </div>
                    
                </div>
                <div className="shop-list">
                    <h3><i></i></h3>
                    <ul>
                        {
                            list4.map((item,index)=>{
                                return(
                                    <Link to={'/detail?'+item.goodsId} key={index}>
                                        <li>
                                            <img src={item.imgUrl} alt=""/>
                                            <h4><sub></sub>{item.crazyPrice}<s><sub></sub>{item.shopPrice}</s></h4>
                                            <span style={{color:'#cd0000',fontSize:'12px',display:'bloak'}}>{item.flashPrice}</span>
                                            <p>{item.goodsName}</p>
                                        </li>
                                    </Link>
                                )
                            })
                        }
                    </ul>
                </div>
               
            </div>
        )
    }
}


