import React,{Component} from 'react';
import './index.scss'
import {connect} from 'react-redux'
import {Toast} from 'antd-mobile'
import { ajaxSend, get_search ,get_goods} from '../../actions';
import {Link} from 'react-router-dom'
import { GoHome } from '../../components/gohome';
@connect(
    state=>{
        return{
            searchValue:state.searchValue,
            hot:state.hot ,
            list4:state.list4,
        }
    }
   
)
export default class Search extends Component{
    goback=()=>{
        const {history}=this.props
        history.go(-1)
    }
    search=()=>{
        const {dispatch} = this.props;
        console.log(this.refs)
        console.log(this.refs.searchInp)
        var  inp=this.refs.searchInp.value
        // if(key){
        //     inp=key
        // }
        console.log(inp)
        Toast.loading("...",10);
        dispatch(ajaxSend({
            config:{
                method:'get',
                    params:{
                        inp
                },
                url:'/search'
            },
            success(data){
                console.log(data)
                if(!data){
                    Toast.hide();
                Toast.offline("......",1) 
                }else{
                    Toast.hide();
                    Toast.success("!",1)
                    dispatch(get_search(data));
                }  
            }
        }))
        // this.refs.searchInp.value=''
    }
    componentWillMount(){
        Toast.loading("...",3);
        const {dispatch,history} = this.props;
        dispatch(ajaxSend({
            config:{
                method:"get",
                params:{
                    limit:18
                },
                url:'/goods'
            },
            success(data){
                
                dispatch(get_goods(data))
                Toast.hide();
                Toast.success("!",1)
                console.log(11111)
            }
        }))
    }
    render(){
        
        const {hot,searchValue,list4}=this.props;
        console.log(list4)
        console.log(searchValue)
        return(
            <div>
                <div className="s-header">
                    <i className='iconfont icon-zuo' onClick={this.goback}></i>
                    <div className="s-input">
                        <input 
                        ref='searchInp'
                         type="text" 
                         placeholder=''
                         />
                    </div>
                    <i className='iconfont icon-sousuo-' onClick={this.search}></i>
                </div>
               <div className="s-hot">
                    <h3><i className='iconfont icon-rexiaochanpin'></i></h3>
                    {/* <div className="hotting">
                       {
                            hot.map((h,index)=>{
                                return(
                                    <span key={index} onClick={()=>this.search(h)}>{h}</span>
                                )
                            })
                       }
                    </div> */}
               </div>
               <div className="shop-list" style={searchValue==false?{display:'bloak'}:{display:'none'}}>
                    <ul>
                        {
                            list4.map((item,index)=>{
                                return(
                                    <Link to={'/detail?'+item.goodsId} key={index}>
                                        <li>
                                            <img src={item.imgUrl} alt=""/>
                                            <h4><sub></sub>{item.crazyPrice}<s><sub></sub>{item.shopPrice}</s></h4>
                                            <p style={{color:'#cd0000',fontSize:'12px'}}>{item.flashPrice}</p>
                                            <p>{item.goodsName}</p>
                                        </li>
                                    </Link>
                                )
                            })
                        }
                    </ul>
                </div>
                <div className="s-list shop-list" style={searchValue?{display:'bloak'}:{display:'none'}}>

                <ul>
                        {
                            searchValue.map((item,index)=>{
                                return(
                                    <Link to={'/detail?'+item.goodsId} key={index}>
                                        <li>
                                            <img src={item.imgUrl} alt=""/>
                                            <h4><sub></sub>{item.crazyPrice}<s><sub></sub>{item.shopPrice}</s></h4>
                                            <p style={{color:'#cd0000',fontSize:'12px'}}>{item.flashPrice}</p>
                                            <p>{item.goodsName}</p>
                                        </li>
                                    </Link>
                                )
                            })
                        }
                    </ul> 
                </div>
                <GoHome />
            </div>
        )
    }
}


